import React from "react";

import SignUpReferenceLink from "components/reusable/atoms/links/SignUpReferenceLink";
import { withClickAnalytics } from "hocs/analytics";
import heroImage from "assets/img/smart-card-hero.png";
import videoPaths from "assets/video";

const ApplyButton = withClickAnalytics(
  "Button",
  "rewards.hero.signup",
)(SignUpReferenceLink);
import yc from "assets/img/logos/not-brex/grayscale/light-text/y-combinator.png";
import sofi from "assets/img/logos/not-brex/grayscale/light-text/sofi.png";
import flexport from "assets/img/logos/not-brex/grayscale/light-text/flexport.png";
import outdoorVoices from "assets/img/logos/not-brex/grayscale/light-text/outdoor-voices.svg";
import carta from "assets/img/logos/not-brex/grayscale/light-text/carta.png";
import boxed from "assets/img/logos/not-brex/grayscale/light-text/boxed.png";

import { Row, Column } from "components/Grid.tsx";

import {
  Container,
  Content,
  Title,
  Subtitle,
  Primary,
  ButtonWrapper,
  TrustedByTitle,
  TrustedByContainer,
  Footnote,
  Logo,
  LogoWrapper,
  Video,
  VideoWrapper,
  TopImage,
} from "./elements";

const logos = [
  { key: "yc", file: yc, height: 45, width: 158 },
  { key: "sofi", file: sofi, height: 30, width: 86 },
  { key: "flexport", file: flexport, height: 28, width: 125 },
  { key: "outdoorVoices", file: outdoorVoices, height: 23, width: 190 },
  { key: "carta", file: carta, height: 50, width: 100 },
  { key: "boxed", file: boxed, height: 27, width: 140 },
];

const Hero = ({
  withVideo = true,
  backgroundImagePath,
  title = (
    <span>
      The smartest
      <Primary> corporate card </Primary>
      in the room
    </span>
  ),
  subtitle = (
    <div>
      No personal guarantee, instant approvals, and modern payment terms.
      <br />
      Powered by the Mastercard network with best-in-class fraud protection.
    </div>
  ),
  extraTitleLogo = undefined,
  footNoteText = "Signing up will not affect your credit score.",
}) => (
  <Container backgroundImagePath={backgroundImagePath}>
    {withVideo && (
      <VideoWrapper>
        <Video
          playsInline
          autoPlay
          loop
          muted
          poster={heroImage}
          src={videoPaths.homeHeroVideo}
        />
      </VideoWrapper>
    )}
    <Row>
      <Column span={1} />
      <Column span={11} spanSmall={10}>
        <Content>
          {extraTitleLogo && (
            <TopImage src={extraTitleLogo} alt="Brex and Payoneer" />
          )}
          <Title>{title}</Title>
          <Subtitle>{subtitle}</Subtitle>
          <ButtonWrapper>
            <ApplyButton
              className="btn btn-cta btn-primary text-white mb-3 mb-sm-0 mr-md-3"
              role="button"
            >
              SIGN UP
            </ApplyButton>
          </ButtonWrapper>
          <Footnote>{footNoteText}</Footnote>
        </Content>
      </Column>
      <Column spanSmall={1} />
    </Row>
    <Row>
      <Column span={1} spanSmall={0} />
      <Column span={10} spanSmall={12}>
        <TrustedByTitle>TRUSTED BY</TrustedByTitle>
        <TrustedByContainer>
          {logos.map((logo) => (
            <LogoWrapper key={logos.key}>
              <Logo src={logo.file} height={logo.height} width={logo.width} />
            </LogoWrapper>
          ))}
        </TrustedByContainer>
      </Column>
      <Column span={1} spanSmall={0} />
    </Row>
  </Container>
);

export default Hero;

